
























import Vue from 'vue'

import { sum } from 'lodash'

export default Vue.extend({
  name: 'PrimaryAttribute',
  props: ['name', 'slug', 'attr', 'fullName'],
  computed: {
    id() {
      return `${this.slug}-container`
    },
    modifier() {
      return sum(this.attr.modifiers)
    }
  }
})
