

















import Vue from 'vue'

export default Vue.extend({
  props: ['screen'],
  data() {
    return {
      picked: null
    }
  },
  methods: {
    close(doubleClickOption) {
      const option = doubleClickOption || this.picked
      if (option) {
        this.screen.onInput(option)
      }
    },
    onEvent(event) {
      switch(event.key) {
      case 'a':
        this.picked = this.screen.options[0]
        break
      case 'b':
        this.picked = this.screen.options[1]
        break
      case ' ':
      case 'Enter':
        this.close()
      default:
        return
      }
    },
    iconPath(professionId: number) {
      return 'assets/professions/' + [
        'acrobatic.svg',
        'button-finger.svg',
        'disintegrate.svg',
        'flying-fox.svg',
        'amputation.svg',
        'catch.svg',
        'divert.svg',
        'fruiting.svg',
        'annexation.svg',
        'conversation.svg',
        'dodging.svg',
        'grab.svg',
        'arrest.svg',
        'convince.svg',
        'drinking.svg',
        'journey.svg',
        'back-forth.svg',
        'coronation.svg',
        'drop-weapon.svg',
        'juggler.svg',
        'backstab.svg',
        'crafting.svg',
        'drowning.svg',
        'jump-across.svg',
        'boot-stomp.svg',
        'crush.svg',
        'eating.svg',
        'kindle.svg',
        'bowman.svg',
        'discussion.svg',
        'expander.svg',
        'kneeling.svg',
      ][professionId]
    }
  }
})
